.footer {
  padding: 29px 20px;

  background-color: #F7F7F7;
}

.footer p {
    max-width: 246px;
    margin: auto;

    text-align: center;
}

.copyrights {
  font-size: 14px;
  line-height: 1.5;

  color: #545454;
}
@media screen and (min-width: 768px) {
  .footer p {
    max-width: 100%;
  }

  .copyrights {
    font-size: 16px;
    line-height: 1.88;
  }
}
